.wrapper
    width: 95%
    margin: 0 auto
    display: flex
    justify-content: space-between
    align-items: center

.logo_wrapper
    height: 45px
    width: 45px

.header
    background-color: rgba(0, 0, 0)
    padding: 10px 0px 10px 0px
    font-family: 'Montserrat', sans-serif
    font-weight: 500

.links_wrapper
    display: flex
    gap: 35px

.link
    a
        color: white
        position: relative
        text-decoration: none
        outline: none
        font-size: 15px
        user-select: none
        &.active::after
            content: ''
            position: absolute
            top: 20px
            left: 0
            width: 100%
            height: 3px
            border-radius: 10px
            background-color: rgba(0, 128, 0, 0.9)
