*,
*::before,
*::after 
  box-sizing: border-box


ul[class],
ol[class] 
  padding: 0


body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd 
  margin: 0


html 
  overflow-x: hidden
  margin-right: calc(-1 * (100vw - 100%))


body 
  min-height: 100vh
  scroll-behavior: smooth
  text-rendering: optimizeSpeed
  line-height: 1.5

body, html
  width: 100%
  height: 100%


ul[class],
ol[class] 
  list-style: none


a:not([class]) 
  text-decoration-skip-ink: auto


img 
  max-width: 100%
  display: block


article > * + * 
  margin-top: 1em


input,
button,
textarea,
select 
  font: inherit

body>svg
  position: absolute



.app 
  overflow: hidden


@media (prefers-reduced-motion: reduce) 
  * 
    animation-duration: 0.01ms !important
    animation-iteration-count: 1 !important
    transition-duration: 0.01ms !important
    scroll-behavior: auto !important
  


@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap')
@import url('https://fonts.googleapis.com/css2?family=Montserratwght@500&&display=swap')
