.wrapper
    display: flex
    align-items: center
    flex-direction: column
    margin-top: 95px
    position: relative
    &::before
        content: ''
        position: absolute
        top: 0
        bottom: -15px
        left: 0
        right: 0
        z-index: -1
        height: 130%
        width: 68%
        margin: auto
        background: #b5b5b594
        border-radius: 50px

.back
    height: 91.2vh
    position: relative
    align-items: center
    display: flex
    flex-direction: column
    &::before
        content: ''
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 0
        background: center/cover url(./../../assets/images/back.jpg) no-repeat
        filter: blur(7px)
        z-index: -50

.content_wrapper
    display: flex
    align-items: center
    justify-content: center
    overflow: hidden
    position: relative
    z-index: 2
    width: 60%
    height: 250px

.menu_wrapper
    height: 30px

.frame
    user-select: none

.calculator_wrapper
    margin-top: 80px
    width: 100%
    position: relative
    &::before
        content: ''
        position: absolute
        top: 0
        bottom: 0
        left: 0
        right: 0
        z-index: -1
        height: 135%
        width: 68%
        margin: auto
        background: #b5b5b594
        border-radius: 50px
