.wrapper
    display: flex
    gap: 12px
    label
        input
            display: none
            &:checked ~ span
                background-color: rgb(118,236,119)
                color: black
                box-shadow: 0 0 5px #76ec77, 0 0 12px #76ec77, 0 0 16px #76ec77
        span
            margin: 15px 0px 15px 0px
            border-radius: 15px
            display: flex
            align-items: center
            justify-content: center
            width: 100px
            padding: 8px
            font-size: 18px
            background-color: black
            color: white
            user-select: none
            transition: all .5s ease-in-out
