:root
    --backlight-color: #ff96ad

%flex-center
    display: flex
    align-items: center
    justify-content: center

.title_wrapper
    position: absolute
    top: 85%
    left: 0%
    bottom: 0
    right: 0
    width: 100%
    height: 27px
    @extend %flex-center

.title
    @extend %flex-center
    color: white
    font-size: 21px
    span
        text-align: center
        user-select: none
        position: relative
        z-index: 1
        border: none
        letter-spacing: 1.1px
    input
        text-align: center
        user-select: none
        outline: none
        border: none
        background: inherit
        box-shadow: none
        color: inherit
        letter-spacing: 1.1px

.logo
    position: absolute
    width: 20px
    height: 20px
    right: 0
    transform: translate(-4vw, 0)

.backlight
    text-shadow: 0 0 1px var(--backlight-color), 0 0 2px var(--backlight-color), 0 0 6px var(--backlight-color), 0 0 8px var(--backlight-color), 0 0 10px var(--backlight-color), 0 0 12px var(--backlight-color), 0 0 14px var(--backlight-color), 0 0 16px var(--backlight-color), 0 0 18px var(--backlight-color), 0 0 20px var(--backlight-color)

.icon_wrapper
    @extend %flex-center
