.container
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center

.wrapper
    display: flex
    gap: 25px
    label
        color: white
        position: relative
        input
            display: none
            &:checked ~ span
                color: #76ec77
        button
            display: flex
            align-items: center
            justify-content: center
            border: none
            color: white
            cursor: pointer
            background: black
            border-radius: 10px
            &:active
                color: white
                background: black

.wrapper
    label:not(:first-child):after
        content: ""
        position: absolute
        top: 0
        bottom: 0
        left: -13px
        right: 0
        height: 100%
        background: white
        width: 1px

.colorPicker_container
    margin-top: 15px
