.icons_wrapper
    display: flex
    align-items: center
    justify-content: center
    gap: 15px
    padding-top: 15px

.wrapper
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column

.icon
    cursor: pointer
    width: 30px
    height: 30px
    transition: all .5s ease-in-out
    &:hover
        transform: scale(1.1)
