.range
    display: flex
    width: 80px
    height: 35px
    background-color: grey
    border-radius: 20px
    border: 1px solid #a3a3a3
    cursor: pointer
    transition: background .5s ease-in-out

.active_range
    background: #76ec77

.thumb
    width: 43%
    height: 100%
    background: #fff
    border-radius: 50%
    cursor: pointer
    transition: margin-left .2s ease-in-out

.active_thumb
    margin-left: 45px
